import { Injectable } from '@angular/core';
import { ICTInterface } from '../../models/ict.interface';
import  LpButton  from '../../../../assets/data/lp-button.json';
import  Buttons  from '../../../../assets/data/buttons.json';
import  SampleProLang  from '../../../../assets/data/sample-procurement.json';
import { IButtonInterface } from '../../models/IButtonInterface';


@Injectable({
  providedIn: 'root'
})
export class LandingPageService {

  constructor() { }

  /**
   * @description Getting the title header text and upload button description used on the landing page from the json file
   * @returns {buttonHeader:string, uploadDesc:string}
   */
  getLpButtonData(): {buttonHeader:string, uploadDesc:string}{
    return LpButton;
  }

  /**
   * @description Getting the label, the description and destination text for the get stated button on the landing page
   * @returns IButtonInterface
   */
  getLpActionButton(): IButtonInterface{
    return Buttons['getStarted'];
  }

  /**
   * @description Getting the data for the Sample Procurement Language in 3 Steps in the json file
   * @returns string
   */
   getSampleProLang(): string{
    return SampleProLang['data'];
  }
  
}
